import { ConnectorOrigin } from '@xip-online-data/types';
import { z } from 'zod';

export interface InputParameterInterface {
  name: string;
  type: 'string' | 'number' | 'boolean' | 'array';
  required?: boolean;
  items?: Array<InputParameterInterface>;
}

export interface ActionInterface {
  identifier: string;
  version: string;
  tenantIdentifier: string;
  name: string;
  description?: string;
  connectorIdentifier: string;
  config: {
    [key: string]: { [key: string]: string | object } | string | object;
  };
  inputParameters: Array<InputParameterInterface>;
  outputParameters: unknown;
  mode?: ConnectorOrigin;
  createdAt: Date;
}

export type CreateActionInterface = Omit<ActionInterface, 'mode' | 'createdAt'>;

export const ActionSchema = z.object({
  identifier: z.string(),
  version: z.string(),
  name: z.string(),
  description: z.string().optional(),
  config: z.record(z.any()),
  inputParameters: z.array(
    z.object({
      name: z.string(),
      type: z.enum(['string', 'number', 'boolean', 'array']),
      required: z.boolean().optional(),
      items: z.array(z.any()).optional(),
    }),
  ),
  outputParameters: z.any(),
  mode: z.nativeEnum(ConnectorOrigin).optional(),
  createdAt: z.date().optional(),
  updatedAt: z.date().optional(),
});
